import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  ComparatorEnum,
  DefaultSortProps,
  enumColumn,
  FilterType,
  getExtraData,
  OrderType,
  referenceColumn,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../dependencies/empresaMFPDependency';
import { CLASS_NAME, DebitoCreditoDarf } from '../../../../models/dtos/mfp/debitoCreditoDarf';
import { GUIA_DARF_ENUM_VALUES } from '../../../../models/enumerated/mfp/guiaDarfEnum';
import { TIPO_DEBITO_CREDITO_ENUM_VALUES } from '../../../../models/enumerated/mlf/tipoDebitoCreditoEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'referencia',
    order: OrderType.DESC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const DebitoCreditoDarfSearchView: FC<SearchViewProps<DebitoCreditoDarf>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const normalizeExpressions = () => {
    if (globalReducer.globalParameter.empresaMFP) {
      return [
        {
          comparator: ComparatorEnum.EQ,
          fieldName: 'empresaMFP.id',
          value: globalReducer.globalParameter.empresaMFP.id,
        },
      ];
    }
  };

  const searchProps = {
    ...props.searchDataGridProps,
    ...{
      searchExpressions: normalizeExpressions,
    },
  };

  const columns: ColumnsProps[] = [
    referenceColumn({
      dataField: 'referencia',
      editable: false,
      sort: true,
      text: 'Referência',
    }),
    enumColumn(
      {
        dataField: 'tipoDebitoCredito',
        editable: false,
        filter: {
          type: FilterType.SELECT,
        },
        sort: true,
        text: 'Tipo',
      },
      TIPO_DEBITO_CREDITO_ENUM_VALUES
    ),
    {
      dataField: 'valor',
      editable: false,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Valor',
    },
    enumColumn(
      {
        dataField: 'tipoDarf',
        editable: false,
        filter: {
          type: FilterType.SELECT,
        },
        sort: true,
        text: 'Tipo de Darf',
      },
      GUIA_DARF_ENUM_VALUES
    ),
  ];

  return (
    <WindowComponent
      title="Manutenção de Débito/Crédito da DARF"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMFP(globalReducer.globalParameter)]}
    >
      <SearchDataGrid
        {...props}
        {...searchProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'DebitoCreditoDarfEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default DebitoCreditoDarfSearchView;
